import {
  createRouter,
  createWebHashHistory
} from 'vue-router'

import main from "../views/main.vue"
import user2 from "../components/user2.vue"
import userSet from "../components/userSet.vue"
import userSet2 from "../components/userSet2.vue"
const routes = [{
  path: "/",
  component: main
}, {
  path: "/user2",
  component: user2
}, {
  path: "/userSet",
  component: userSet
}, {
  path: "/userSet2",
  component: userSet2
}]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router